*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 16px;
}

html,
body,
#root {
  height: 100%;
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
}

figure {
  margin: 0;
}

.film-library {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  z-index: 0;
}

.film-list {
  position: relative;
  -ms-flex: 1 0 33.333333%;
      flex: 1 0 33.333333%;
  padding: 3.64em 0 0;
  background-color: #f0f0f0;
}

.film-list > .section-title {
  width: 33.333333%;
  left: 0;
}

.film-details {
  -ms-flex: 0 1 66.666666%;
      flex: 0 1 66.666666%;
  background-color: #f0f0f0;
}

.film-rows {
  height: 100vh;
  overflow-y: scroll;
  padding: 0 0 3.64em;
}

.film-row {
  display: grid;
  position: relative;
  grid-template-columns: 1fr 3fr;
  margin-bottom: 0.2rem;
  font-size: 0.5em;
  background-color: white;
}

.film-row:hover {
  cursor: pointer;
}

.film-row-fave {
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 2rem;
  font-weight: 800;
  padding: 0 0.4em;
  background-color: transparent;
  -webkit-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
}

.film-row-fave.remove_from_queue {
  background-color: rgba(32, 178, 170, 0.75);
}

.film-poster {
  margin: 0;
}

.film-summary {
  padding: 0.5em 1.5em;
  background-color: white;
}

.film-summary h1 {
  font-size: 2em;
  margin-bottom: 0.35em;
}

.film-summary p {
  font-size: 1.6em;
  line-height: 1.35;
  font-weight: 200;
  margin-top: 0;
}

.film-summary-text {
  display: inline-block;
  width: 80%;
  overflow: none;
}

.section-title {
  position: fixed;
  top: 0;
  z-index: 1;
  background-color: #777;
  color: white;
  text-align: center;
  font-size: 0.75em;
  padding: 0.5em;
  margin: 0 0 0.1em;
  letter-spacing: 0.3em;
  text-transform: uppercase;
}

.film-list-filters {
  position: fixed;
  top: 1.6em;
  left: 0;
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 33.333333%;
}

.film-list-filter {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  background-color: #ccc;
  color: white;
  text-align: center;
  font-size: 0.75em;
  padding: 0.5em;
  margin: 0 0 0.1em;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  cursor: pointer;
}

.film-list-filter:hover {
  background-color: hsl(177, 70%, 46%);
}

.film-list-filter.is-active {
  background-color: lightseagreen;
}

.section-count {
  display: inline-block;
  background-color: white;
  color: black;
  text-align: center;
  padding: 0.25em 0.25em 0.25em 0.5em;
  border-radius: 20%;
  margin-left: 0.5em;
  font-weight: bold;
}

.film-details {
  position: relative;
  overflow-y: scroll;
}

.film-details > .section-title {
  left: 33.333333%;
  width: 66.666666%;
}

.film-detail {
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow-y: scroll;
  margin-bottom: 2em;
}

.film-detail.is-hydrated {
  display: block;
}

.film-backdrop {
  margin-top: 1.6em;
  position: relative;
}

.film-title {
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0;
  color: white;
  font-size: 4em;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0,0,0,0.5)));
  background: -webkit-linear-gradient(top, transparent, rgba(0,0,0,0.5));
  background: -o-linear-gradient(top, transparent, rgba(0,0,0,0.5));
  background: linear-gradient(180deg, transparent, rgba(0,0,0,0.5));
  padding: 0.25em 1.5rem;
}

.film-tagline {
  line-height: 1.2;
}

.film-detail-poster {
  float: right;
  width: 20%;
  margin-left: 2em;
  -webkit-box-shadow: 0 0 30px rgba(0, 0, 0, 0.35);
          box-shadow: 0 0 30px rgba(0, 0, 0, 0.35);
}

.film-meta {
  padding: 1em 1.5em;
  line-height: 1.45;
  color: white;
  background-color: lightseagreen;
}

@media screen and (max-width: 800px) {
  .film-title {
    font-size: 2.4em;
  }

  .film-tagline {
    font-size: 1.15em;
  }
}



